import { Alert, Button, Input, Select } from "@windmill/react-ui";
import { useTranslation } from "react-i18next";
import Error from "components/form/Error";
import spinnerLoadingImage from "assets/img/spinner.gif";
import InputAreaTwo from "components/form/InputAreaTwo";
import PageTitle from "components/Typography/pageTitleButton";
import useSettingSubmit from "hooks/useeditStaypackages";
import Uploader from "components/image-uploader/Uploader";
import {
  getAllCommonActivityList,
  getAllCommonActivityListByproperyID,
  getAllCommonBuffectsList,
  getAllCommonProperyList,
  getAllStayListByPropertyId,
  staySinglepackagesDetails,
} from "services/Api";
import { useEffect, useState } from "react";
import InputArea from "components/form/InputArea";
import Styles from "./Styles";
import { useParams } from "react-router";
import { Form, Field } from "react-final-form";
import arrayMutators from "final-form-arrays";
import { FieldArray } from "react-final-form-arrays";
import DatePicker from "react-multi-date-picker";
import DatePanel from "react-multi-date-picker/plugins/date_panel";
import moment from "moment";

const Setting = () => {
  const { id, pid } = useParams();
  const {
    errors,
    register,
    handleSubmit,
    onSubmit,
    resetBtn,
    imageUrl,
    setImageUrl,
    SingleStaypackagesDetailList,
    setSingleStaypackagesDetailList,
    setValue,
    isSave,
    storedFirstValue,
    setstoredFirstValue,
    isSubmitting,
    setDates,
    dates,
    watch,
    dates1,
    setDates1,
  } = useSettingSubmit("ff", pid);
  const { t } = useTranslation();
  const [propertyList, setpropertyList] = useState([]);
  const [staylist, setstaylist] = useState([]);
  const [Activitylist, setActivitylist] = useState([]);
  const [Buffectslist, setBuffectslist] = useState([]);
  const [loading, setLoading] = useState(false);
  const daysWatch = watch("SelectDays");
  const [initialValues, setinitialValues] = useState({
    Package_itinerary: [
      {
        name: "",
        timing: "",
        day_1: true,
        day_2: false,
      },
    ],
  });
  const setDates2 = (e) => {
    setDates(e?.map((date) => date?.format("MM/DD/YYYY")));
  };
  const setDates3 = (e) => {
    setDates1(e?.map((date) => date?.format("MM/DD/YYYY")));
  };

  const [formData, setFormData] = useState(initialValues);
  useEffect(() => {
    getPropertyList();
    getStayList();
    // getActivityList();
    getBuffectsList();
  }, []);

  useEffect(() => {
    getSingleStayPackages(id);
  }, [id]);

  const getSingleStayPackages = async (id) => {
    const staysinglepackagesDetails = await staySinglepackagesDetails(id);
    await getActivityList(
      staysinglepackagesDetails.data.data?.stayPackage?.Property?.id?.toString()
    );

    const stayData = staysinglepackagesDetails.data.data?.stayPackage;

    setLoading(false);
    getStayList(
      staysinglepackagesDetails.data.data?.stayPackage?.Property?.id?.toString()
    );
    setSingleStaypackagesDetailList(
      staysinglepackagesDetails.data.data?.stayPackage
    );
    setValue("PropertyId", stayData.Property?.id?.toString());
    setValue("StayID", stayData?.stay_id);
    setValue("name", stayData?.name);
    setValue("Checkindate", stayData?.check_in_time);
    setValue("Checkouttime", stayData?.check_out_time);
    setValue("SelectDays", stayData?.days);
    setValue("Badge", stayData?.badge);
    // setValue("price_child", stayData?.price_child);
    setValue("food_base", stayData?.food_base);
    setValue("child_food_base", stayData?.child_food_base);
    setValue("activity_base", stayData?.activity_base);
    setValue("child_activity_base", stayData?.child_activity_base);
    setValue("accomodation_base", stayData?.accomodation_base);
    setValue("child_accomodation_base", stayData?.child_accomodation_base);
    setValue("cgst", stayData?.cgst);
    setValue("sgst", stayData?.sgst);
    setValue("TotalAvailblity", stayData?.slots_availability);
    // setValue("additional_price_per_child", stayData?.price_child);
    setValue(
      "buffets",
      stayData?.Package_buffets?.map((loc) => loc?.Buffet?.id.toString())
    );
    // setValue(
    //   "additional_price_per_person",
    //   stayData?.additional_price_per_person
    // );
    setValue("Amount", stayData?.price);
    // setActivitylist(stayData?.Package_activities?.map((item) => item?.Activity))
    setValue(
      "Activities",
      stayData?.Package_activities?.map((loc) => loc?.Activity?.id.toString())
    );
    // setValue(
    //   "buffets",
    //   stayData?.Package_buffets?.map((item) => item?.buffet_id)
    // );

    setinitialValues({ Package_itinerary: stayData?.Package_itineraries });

    setstoredFirstValue({ Package_itinerary: stayData?.Package_itineraries });
    setImageUrl(
      staysinglepackagesDetails?.data.data?.stayPackage?.Package_images
    );
    setDates(
      stayData?.Package_block_dates?.map((item) =>
        moment(item?.date).format("MM/DD/YYYY")
      )
    );
    setDates1(
      stayData?.Package_special_dates?.map((item) =>
        moment(item?.date).format("MM/DD/YYYY")
      )
    );
  };

  const getPropertyList = async () => {
    const PropertyList = await getAllCommonProperyList();
    console.log(
      PropertyList?.data?.data?.propertyList,
      "getAllcommonProperyList"
    );
    setpropertyList(PropertyList?.data?.data?.propertyList);
    setLoading(false);
  };
  const handleSelectChange = (e) => {
    console.log(e.target.value, "data");
    // setpropertyID(e.target.value);
    // getAllkarte(e.target.value);
    // getAllactivityList(e.target.value);
    // getPackagesList(event, e.target.value);
    getStayList(e.target.value);
    getActivityList(e.target.value);
  };
  const getStayList = async (id) => {
    const staylistData = await getAllStayListByPropertyId(id);
    console.log(staylistData?.data?.data?.stays, "stay");
    setstaylist(staylistData?.data?.data?.stays);
    setLoading(false);
  };

  const getActivityList = async (id) => {
    const ActivityData = await getAllCommonActivityListByproperyID(id);
    setActivitylist(ActivityData?.data?.data?.activity);
    // setValue(
    //   "Activities",
    //   ActivityData?.data?.data?.activity?.map((item) => item?.activity_id)
    // );
    setLoading(false);
    return true;
  };

  const getBuffectsList = async () => {
    const BuffectsData = await getAllCommonBuffectsList();
    console.log(BuffectsData?.data?.data?.buffets, "BuffectsData");
    setBuffectslist(BuffectsData?.data?.data?.buffets);
    setLoading(false);
  };
  const containsSpecialCharsOrNumbers = (value) => {
    return /[!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?0-9]/.test(value);
  };

  const storedetails = (values) => {
    console.log("Form Data:", values);

    setFormData(values);
    setstoredFirstValue(values);
    setinitialValues(values);
  };
  return (
    <>
      <PageTitle resetBtn={resetBtn}>{t("Edit Stay Package")}</PageTitle>

      <div className="container p-6 mx-auto bg-white  dark:bg-gray-800 dark:text-gray-200 rounded-lg">
        <form onSubmit={handleSubmit(onSubmit)}>
          <div className="grid grid-cols-12 font-sans">
            <div className="col-span-12 md:col-span-12 lg:col-span-12 mr-3 ">
              <div className="flex-grow scrollbar-hide w-full max-h-full pb-0">
                <div className="grid md:grid-cols-6 items-center sm:grid-cols-12 gap-3 md:gap-5 xl:gap-4 lg:gap-4 mb-6">
                  <div className="sm:col-span-3">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Select Property")}
                    </label>

                    <div className="sm:col-span-3">
                      <Select
                        {...register(`PropertyId`, {
                          required: "Select Property is required !",
                        })}
                        isClearable={true}
                        className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                        onChange={(e) => handleSelectChange(e)}
                      >
                        {/* {SingleStaypackagesDetailList?.Stay_room_aminities?.map((amm)=> ( <option value={amm?.Stay_aminity?.id} selected>
                      {amm?.Stay_aminity?.name}
                          </option>))} */}

                        <option
                          value={SingleStaypackagesDetailList.Property?.id}
                          defaultValue
                          hidden
                        >
                          {SingleStaypackagesDetailList.Property?.name}
                        </option>
                        {propertyList?.map((pro) => (
                          <option value={pro.id}>{pro.name}</option>
                        ))}
                      </Select>
                      {/* <Error errorName={errors.PropertyId} pos /> */}
                    </div>
                  </div>
                  <div className="sm:col-span-3">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Select Stay")}
                    </label>

                    <div className="sm:col-span-3">
                      <Select
                        {...register(`StayID`, {
                          required: "Select Stay is required !",
                        })}
                        className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                      >
                        <option
                          value={SingleStaypackagesDetailList?.stay_id}
                          defaultValue
                          hidden
                        >
                          {SingleStaypackagesDetailList?.Stay?.name}
                        </option>
                        {staylist?.map((pro) => (
                          <option value={pro.id}>{pro.name}</option>
                        ))}
                      </Select>
                      <Error errorName={errors.StayID} pos />
                    </div>
                  </div>
                </div>

                <div className="grid md:grid-cols-6 items-center sm:grid-cols-12 gap-3 md:gap-5 xl:gap-4 lg:gap-4 mb-6">
                  <div className="sm:col-span-3">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Name")}
                    </label>
                    <div className="sm:col-span-3">
                      <Input
                        {...register(`name`, {
                          required: "Resort name required!",
                          validate: (value) =>
                            !containsSpecialCharsOrNumbers(value) ||
                            "Name cannot contain special characters or numbers",
                        })}
                        label={t("Name")}
                        name="name"
                        defaultValue={SingleStaypackagesDetailList?.name}
                        type="text"
                        placeholder={t("Enter Name")}
                        className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                      />
                      <Error errorName={errors.name} pos />
                    </div>
                  </div>
                  <div className="sm:col-span-3">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Select Days")}
                    </label>

                    <div className="sm:col-span-3">
                      <Select
                        {...register(`SelectDays`, {
                          required: "Select Days is required !",
                        })}
                        className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                      >
                        <option value="" defaultValue hidden>
                          {SingleStaypackagesDetailList?.days}
                        </option>
                        <option value="all">All days</option>
                        <option value="weekdays">Week days</option>
                        <option value="weekends">Weekends</option>
                        <option value="special_holidays">Special Days</option>
                      </Select>
                      <Error errorName={errors.SelectDays} pos />
                    </div>
                  </div>
                </div>

                <div className="grid md:grid-cols-6 items-center sm:grid-cols-12 gap-3 md:gap-5 xl:gap-4 lg:gap-4 mb-6">
                  <div className="sm:col-span-3">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Check In Time")}
                    </label>
                    <div className="flex1">
                      <div className="sm:col-span-5 w-260">
                        <InputAreaTwo
                          register={register}
                          label={t("Check in time")}
                          name="Checkindate"
                          defaultValue={
                            SingleStaypackagesDetailList?.check_in_time
                          }
                          type="time"
                          placeholder={t("Check in Time")}
                        />
                        <Error errorName={errors.Checkindate} pos />
                      </div>
                      <div
                        className="sm:col-span-5"
                        style={{ marginTop: "-25px" }}
                      >
                        <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                          {t("Check Out Time")}
                        </label>
                        <div className="sm:col-span-5 w-260">
                          <InputAreaTwo
                            register={register}
                            label={t("Check out time")}
                            defaultValue={
                              SingleStaypackagesDetailList?.check_out_time
                            }
                            name="Checkouttime"
                            type="time"
                            placeholder={t("Check out Time")}
                          />
                          <Error errorName={errors.Checkouttime} pos />
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="sm:col-span-3">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Select Activities")}
                    </label>

                    <div className="sm:col-span-3">
                      <Select
                        {...register(`Activities`, {
                          // required: "Select Activities is required !",
                        })}
                        multiple
                        className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                      >
                        {/* {SingleStaypackagesDetailList?.Package_activities?.map(
                          (amm) => (
                            <option value={amm?.Activity?.id} selected>
                              {amm?.Activity?.name}
                            </option>
                          )
                        )} */}
                        <option value="" hidden>
                          {t("Select Activities")}
                        </option>
                        {Activitylist?.map((pro) => (
                          <option value={pro.id}>{pro.name}</option>
                        ))}
                      </Select>
                      <Error errorName={errors.Activities} pos />
                    </div>
                  </div>
                </div>

                <div className="grid md:grid-cols-6 items-center sm:grid-cols-12 gap-3 md:gap-5 xl:gap-4 lg:gap-4 mb-6">
                  <div className="sm:col-span-3">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Select Buffets")}
                    </label>

                    <div className="sm:col-span-3">
                      <Select
                        {...register(`buffets`, {
                          required: "Select buffets is required !",
                        })}
                        multiple
                        className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                      >
                        {/* {SingleStaypackagesDetailList?.Package_buffets?.map(
                          (amm) => (
                            <option value={amm?.Buffet?.id} selected>
                              {amm?.Buffet?.name}
                            </option>
                          )
                        )} */}
                        <option value="" defaultValue hidden>
                          {t("Select buffets")}
                        </option>
                        {Buffectslist?.map((pro) => (
                          <option value={pro.id}>{pro.name}</option>
                        ))}
                      </Select>

                      <Error errorName={errors.buffets} pos />
                    </div>
                  </div>
                  <div className="sm:col-span-3">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Select Badge")}
                    </label>

                    <div className="sm:col-span-3">
                      <Select
                        {...register(`Badge`, {
                          required: false,
                        })}
                        className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                      >
                        <option value="" defaultValue hidden>
                          {SingleStaypackagesDetailList?.badge}
                        </option>
                        <option value="top_seller">Top Seller</option>
                        <option value="most_loved">Most Viewed</option>
                        <option value="limited_slots">Limited Slots</option>
                        <option value="premium">Premium</option>
                      </Select>
                      <Error errorName={errors.Badge} pos />
                    </div>
                  </div>
                  <div className="sm:col-span-3">
                    <div className="grid md:grid-cols-6 items-center sm:grid-cols-12 gap-3 md:gap-5 xl:gap-4 lg:gap-4 mb-6">
                    <div className="col-span-4 sm:col-span-6">
                        <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                          {t("Food base / Person")}
                        </label>
                        <div className="sm:col-span-3">
                          <Input
                            {...register("food_base", {
                              pattern: {
                                value: /^[0-9]*$/,
                                message: "Please enter a valid number.",
                              },
                            })}
                            onChange={(e) => {
                              const value = e.target.value;
                              setValue("food_base", value);
                              setValue("child_food_base", parseInt(value / 2));
                            }}
                            label={t("food_base")}
                            name="food_base"
                            type="text"
                            placeholder={t("Food base / Person")}
                            className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                          />
                          <Error errorName={errors.food_base} pos />
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="sm:col-span-3">
                    <div className="grid md:grid-cols-6 items-center sm:grid-cols-12 gap-3 md:gap-5 xl:gap-4 lg:gap-4 mb-6">
                      <div className="col-span-4 sm:col-span-6">
                        <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                          {t("Food base / Child")}
                        </label>
                        <div className="sm:col-span-3">
                          <Input
                            {...register("child_food_base", {
                              pattern: {
                                value: /^[0-9]*$/,
                                message: "Please enter a valid number.",
                              },
                            })}
                            disabled
                            label={t("child_food_base")}
                            name="child_food_base"
                            type="text"
                            placeholder={t("Food base / Child")}
                            className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                          />
                          <Error errorName={errors.child_food_base} pos />
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="sm:col-span-3">
                    <div className="grid md:grid-cols-6 items-center sm:grid-cols-12 gap-3 md:gap-5 xl:gap-4 lg:gap-4 mb-6">
                      <div className="col-span-4 sm:col-span-6">
                        <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                          {t("Activity base / Person")}
                        </label>
                        <div className="sm:col-span-3">
                          <Input
                            {...register("activity_base", {
                              pattern: {
                                value: /^[0-9]*$/,
                                message: "Please enter a valid number.",
                              },
                            })}
                            onChange={(e) => {
                              const value = e.target.value;
                              setValue("activity_base", value);
                              setValue(
                                "child_activity_base",
                                parseInt(value / 2)
                              );
                            }}
                            label={t("activity_base")}
                            name="activity_base"
                            type="text"
                            placeholder={t("Activity base / Child")}
                            className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                          />
                          <Error errorName={errors.activity_base} pos />
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="sm:col-span-3">
                    <div className="grid md:grid-cols-6 items-center sm:grid-cols-12 gap-3 md:gap-5 xl:gap-4 lg:gap-4 mb-6">
                      <div className="col-span-4 sm:col-span-6">
                        <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                          {t("Activity base / Child")}
                        </label>
                        <div className="sm:col-span-3">
                          <Input
                            {...register("child_activity_base", {
                              pattern: {
                                value: /^[0-9]*$/,
                                message: "Please enter a valid number.",
                              },
                            })}
                            disabled
                            label={t("child_activity_base")}
                            name="child_activity_base"
                            type="text"
                            placeholder={t("Activity base / Child")}
                            className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                          />
                          <Error errorName={errors.child_activity_base} pos />
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="sm:col-span-3">
                    <div className="grid md:grid-cols-6 items-center sm:grid-cols-12 gap-3 md:gap-5 xl:gap-4 lg:gap-4 mb-6">
                      <div className="col-span-4 sm:col-span-6">
                        <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                          {t("Accommodation base / Person")}
                        </label>
                        <div className="sm:col-span-3">
                          <Input
                            {...register("accomodation_base", {
                              pattern: {
                                value: /^[0-9]*$/,
                                message: "Please enter a valid number.",
                              },
                            })}
                            onChange={(e) => {
                              const value = e.target.value;
                              setValue("accomodation_base", value);
                              setValue(
                                "child_accomodation_base",
                                parseInt(value / 2)
                              );
                            }}
                            label={t("accomodation_base")}
                            name="accomodation_base"
                            type="text"
                            placeholder={t("Accommodation base / Child")}
                            className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                          />
                          <Error errorName={errors.accomodation_base} pos />
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="sm:col-span-3">
                    <div className="grid md:grid-cols-6 items-center sm:grid-cols-12 gap-3 md:gap-5 xl:gap-4 lg:gap-4 mb-6">
                      <div className="col-span-4 sm:col-span-6">
                        <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                          {t("Accommodation base / Child")}
                        </label>
                        <div className="sm:col-span-3">
                          <Input
                            {...register("child_accomodation_base", {
                              pattern: {
                                value: /^[0-9]*$/,
                                message: "Please enter a valid number.",
                              },
                            })}
                            disabled
                            label={t("child_accomodation_base")}
                            name="child_accomodation_base"
                            type="text"
                            placeholder={t("Accommodation base / Child")}
                            className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                          />
                          <Error
                            errorName={errors.child_accomodation_base}
                            pos
                          />
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="grid md:grid-cols-6 items-center sm:grid-cols-12 gap-3 md:gap-5 xl:gap-4 lg:gap-4 mb-6">
                  <div className="sm:col-span-3">
                    <div className="grid md:grid-cols-6 items-center sm:grid-cols-12 gap-3 md:gap-5 xl:gap-4 lg:gap-4 mb-6">
                      <div className="col-span-4 sm:col-span-6">
                        <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                          {"Price Per Package"}
                        </label>
                        <Input
                          {...register("Amount", {
                            required: "Amount required!",
                            pattern: {
                              value: /^[0-9]*$/,
                              message: "Please enter a valid number.",
                            },
                          })}
                          label={"Amount"}
                          name="Amount"
                          type="text"
                          placeholder={t("Enter Amount")}
                          className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                        />
                        <Error errorName={errors.Amount} />
                      </div>
                    </div>
                  </div>
                </div>

                <div className="grid md:grid-cols-6 items-center sm:grid-cols-12 gap-3 md:gap-5 xl:gap-4 lg:gap-4 mb-6">
                  <div className="sm:col-span-6">
                    <div className="grid md:grid-cols-6 items-center sm:grid-cols-12 gap-3 md:gap-5 xl:gap-4 lg:gap-4 mb-6">
                      <div className="col-span-4 sm:col-span-6">
                        <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                          {"Block Dates"}
                        </label>
                        <DatePicker
                          value={dates}
                          //   onChange={setDates}
                          onChange={setDates2}
                          multiple
                          sort
                          minDate={new Date()}
                          format={"MM/DD/YYYY"}
                          placeholder="Please Enter Blocks Date"
                          calendarPosition="bottom-center"
                          plugins={[<DatePanel />]}
                        />
                      </div>
                    </div>
                    {daysWatch === "special_holidays" && (
                      <div className="sm:col-span-3">
                        <div className="grid md:grid-cols-3 items-center sm:grid-cols-12 gap-3 md:gap-5 xl:gap-4 lg:gap-4 mb-6">
                          <div className="col-span-2 sm:col-span-3">
                            <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                              {"Special Dates"}
                            </label>
                            <DatePicker
                              value={dates1}
                              onChange={setDates3}
                              multiple
                              sort
                              required
                              minDate={new Date()}
                              format={"MM/DD/YYYY"}
                              placeholder="Please Enter Blocks Date"
                              calendarPosition="bottom-center"
                              plugins={[<DatePanel />]}
                            />
                          </div>
                        </div>
                      </div>
                    )}
                  </div>
                </div>

                <div className="sm:col-span-3">
                  <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                    {t("Photos")}
                  </label>
                  <div className="grid md:grid-cols-4 items-center sm:grid-cols-4 gap-3 md:gap-5 xl:gap-4 lg:gap-4 mb-4">
                    <div>
                      <Uploader
                        product
                        folder="product"
                        imageUrl={imageUrl}
                        setImageUrl={setImageUrl}
                      />
                      <Alert className="sm12" type="info">
                        Image should be 660 × 475px and Image Size should be
                        less than 5mb
                      </Alert>
                    </div>
                    <Error errorName={errors.totalAmount} pos />
                  </div>
                </div>

                <div className="flex flex-row-reverse pb-6">
                  <Button
                    type="button"
                    className="h-12 px-8"
                    onClick={() => resetBtn()}
                    style={{
                      position: "absolute",
                      bottom: "3em",
                      right: "22em",
                      backgroundColor: "gainsboro",
                      color: "red",
                    }}
                  >
                    Cancel
                  </Button>
                  {isSubmitting ? (
                    <Button
                      disabled={true}
                      type="button"
                      className="h-12"
                      style={{ position: "absolute", bottom: "3em" }}
                    >
                      <img
                        src={spinnerLoadingImage}
                        alt="Loading"
                        width={20}
                        height={10}
                      />{" "}
                      <span className="font-serif ml-2 font-light">
                        Processing
                      </span>
                    </Button>
                  ) : (
                    <Button
                      type="submit"
                      className="h-12 px-8"
                      style={{ position: "absolute", bottom: "3em" }}
                    >
                      {" "}
                      {isSave ? "Save" : "Update Stay Package"}
                    </Button>
                  )}
                </div>
              </div>
            </div>
          </div>
        </form>
      </div>
      <div className="grid md:grid-cols-6 items-center sm:grid-cols-12 gap-3 md:gap-5 xl:gap-4 lg:gap-4 mb-6">
        <div className="sm:col-span-4">
          <Styles>
            <Form
              initialValues={initialValues}
              onSubmit={(values) => storedetails(values)}
              mutators={{
                ...arrayMutators,
              }}
              render={({
                handleSubmit,
                form: {
                  mutators: { push, pop },
                },
                pristine,
                submitting,
              }) => {
                return (
                  <form onSubmit={handleSubmit}>
                    <div className="">
                      <button
                        type="button"
                        style={{ color: "#fff" }}
                        className="align-bottom inline-flex items-center justify-center cursor-pointer leading-5 transition-colors duration-150 font-medium focus:outline-none px-4 py-2 rounded-lg text-sm text-white bg-green-500 border border-transparent active:bg-green-600 hover:bg-green-600 focus:ring focus:ring-purple-300 h-12 px-8"
                        onClick={() =>
                          push("Package_itinerary", {
                            name: "",
                            timing: "",
                            day_1: true,
                            day_2: false,
                          })
                        }
                      >
                        Add Internary
                      </button>
                      {/* <button type="button" onClick={() => pop("customers")}>
                Remove 
              </button> */}
                    </div>
                    <FieldArray name="Package_itinerary">
                      {({ fields }) =>
                        fields.map((name, index) => (
                          <div key={name} className="itinerary-item">
                            <div>
                              <label>
                                <Field
                                  name={`${name}.day_1`}
                                  component="input"
                                  type="checkbox"
                                />{" "}
                                Day 1
                              </label>
                            </div>
                            <div>
                              <label>
                                <Field
                                  name={`${name}.day_2`}
                                  component="input"
                                  type="checkbox"
                                />{" "}
                                Day 2
                              </label>
                            </div>
                            <Field
                              name={`${name}.name`}
                              component="input"
                              placeholder="Name"
                            />
                            <Field
                              name={`${name}.timing`}
                              component="input"
                              placeholder="Timing"
                            />

                            <span
                              onClick={() => fields.remove(index)}
                              style={{ cursor: "pointer" }}
                            >
                              ❌
                            </span>
                          </div>
                        ))
                      }
                    </FieldArray>

                    <Button className="h-12 px-8" type="submit">
                      Update details
                    </Button>
                  </form>
                );
              }}
            />
          </Styles>
        </div>
      </div>
    </>
  );
};
export default Setting;
